/* Reset margins and paddings */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* General styling */
body {
  font-family: 'Poppins', sans-serif;
  background-color: #1b2430; /* Dark background */
  color: white;
}

/* Navbar styling */
.navbar {
  display: flex; /* Enables horizontal alignment */
  justify-content: space-between; /* Space between logo and nav links */
  align-items: center; /* Align logo and links vertically */
  background-color: #1b2430; /* Dark navy background */
  padding: 10px 30px;
  position: sticky;
  top: 0;
  z-index: 1000;
}

/* Logo styling */
.logo {
  color: orange; /* Highlight color for the logo */
  font-size: 39px;
  font-weight: bold;
  margin-top:20px;
}

/* Nav links styling */
.nav-links {
  list-style: none;
  display: flex; /* Align links horizontally */
  gap: 20px; /* Add spacing between links */
  margin: 0;
  padding: 0;
  margin-top:20px;
}

/* Individual link styling */
.nav-links li a {
  text-decoration: none;
  color: #cccccc; /* Light gray for inactive links */
  font-size: 20px;
  font-weight: 500;
  transition: color 0.3s;
}

/* Hover effect for links */
.nav-links li a:hover {
  color: #1da1f2; /* Highlight color for links */
}

/* Homepage container */
.homepage {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: calc(100vh - 60px); /* Full screen minus the navbar */
  padding: 0 10%;
}

/* Left content */
.content {
  max-width: 50%;
}

.content h2 {
  font-size: 24px;
  color: #aaa; /* Subheading color */
  margin-bottom: 10px;
}

.content h1 {
  font-size: 50px;
  margin-bottom: 10px;
}

.content h3 {
  font-size: 24px;
  margin-bottom: 20px;
}

.highlight {
  color: #1da1f2; /* Highlight color */
}

.content p {
  font-size: 16px;
  margin-bottom: 20px;
  line-height: 1.5;
  color: #ddd; /* Light text color */
}


.social-icons {
  display: flex;
  gap: 20px;
  margin-top: 20px;
}

.icon {
  text-decoration: none;
  font-size: 24px;
  color: #1da1f2; /* Blue for icons */
  cursor: pointer;
  transition: color 0.3s ease;
}

.icon:hover {
  color: #0d6efd; /* Slightly darker blue on hover */
}

/* Popup Overlay */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
  display: flex;
  justify-content: center; /* Centers the popup horizontally */
  align-items: center; /* Centers the popup vertically */
  z-index: 1000; /* Ensure it stays above all other elements */
}

/* Popup Box */
.popup-box {
  background: black; /* White background for the box */
  padding: 20px 40px; /* Add padding inside the popup */
  border-radius: 10px; /* Rounded corners for the popup */
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3); /* Subtle shadow for depth */
  position: relative; /* For positioning the close button */
  text-align: center; /* Center the text inside */
  margin-left:40px;
}

/* Close Button */
.close-btn {
  position: absolute;
  top: 10px; /* Position at the top-right corner */
  right: 10px;
  background: none;
  border: none;
  font-size: 20px; /* Larger size for better visibility */
  cursor: pointer;
  color: red; /* Red color for the close button */
  font-weight: bold;
}

.close-btn:hover {
  color: darkred; /* Slightly darker red on hover */
}

/* Email Link Styling */
.popup-box a {
  color: #1da1f2; /* LinkedIn blue for the email link */
  font-size: 18px; /* Slightly larger font */
  text-decoration: none; /* Remove underline */
}

.popup-box a:hover {
  text-decoration: underline; /* Add underline on hover */
}

.download-btn {
  background-color: #1da1f2;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.download-btn:hover {
  background-color: #0d6efd;
}

.download-btn a {
  text-decoration: none;
  color: white; /* Match button text color */
}

/* Profile picture */
.profile-picture {
  max-width: 90%;
  text-align: center;
}

.profile-picture img {
  width: 400px;
  height: 400px;
  border-radius: 50%;
  background: #1da1f2; /* Background color for border */
  padding: 10px;
  box-shadow: 0 0 30px #1da1f2; /* Glow effect */
}

/* Responsive Design */
@media (max-width: 768px) {
  .homepage {
    flex-direction: column; /* Stack content vertically */
    text-align: center;
  }

  .content, .profile-picture {
    max-width: 100%;
  }

  .profile-picture {
    margin-top: 20px;
  }
}
html {
  scroll-behavior: smooth;
}
