/* Work Experience Section */
.section {
  background-color: #1b2430; /* Dark background */
  color: white;
  padding: 60px 20px;
}

.section h2 {
  text-align: center;
  font-size: 32px;
  margin-bottom: 40px;
  color: #1da1f2; /* Blue for emphasis */
}

/* Experience Item */
.experience-item {
  background-color: #242f40; /* Slightly darker background for the box */
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  max-width: 900px;
  margin: 0 auto;
}

/* Timeline Header */
.timeline-header h3 {
  font-size: 20px;
  color: #1da1f2; /* Bright blue */
  margin-bottom: 5px;
}

.timeline-header h4 {
  font-size: 16px;
  color:#1da1f2;
  font-style: italic;
  margin-bottom: 10px;
}

.timeline-date {
  font-size: 14px;
  color: #ddd; /* Light gray for the date */
  margin-bottom: 20px;
  font-weight: bold;
}

/* Experience Points */
.experience-points {
  display: flex;
  flex-wrap: wrap; /* Enable wrapping for smaller screens */
  gap: 15px; /* Space between boxes */
  margin-top: 20px;
}

.point-box {
  background-color: #1b2430; /* Match the section background */
  padding: 15px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  flex: 1 1 calc(50% - 15px); /* Two boxes per row with gap */
  box-sizing: border-box;
  color: #ddd;
  font-size: 14px;
  line-height: 1.6;
}

.point-box p {
  margin: 0;
}

/* Responsive Design */
@media (max-width: 768px) {
  .point-box {
    flex: 1 1 100%; /* Full width for small screens */
  }
}
/* Projects Section */
.projects-section {
  background-color: #1b2430; /* Dark background */
  color: white;
  padding: 60px 20px;
}

.projects-section h2 {
  text-align: center;
  font-size: 32px;
  margin-bottom: 40px;
  color: #1da1f2; /* Highlighted blue */
}

/* Individual Project Items */
.project-item {
  background-color: #242f40; /* Slightly darker background */
  padding: 20px;
  margin-bottom: 30px;
  border-radius: 12px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}

.project-item h3 {
  font-size: 20px;
  color: #1da1f2; /* Bright blue for project titles */
  margin-bottom: 10px;
}

.project-item p {
  font-size: 14px;
  color: #ddd; /* Light gray for text */
  line-height: 1.6;
}

.project-item strong {
  color: #1da1f2; /* Highlight for bold text */
}

.project-item:not(:last-child) {
  margin-bottom: 20px;
}
/* Education Section */
.education-section {
  background-color: #1b2430; /* Dark background */
  color: white;
  padding: 60px 20px;
}

.education-section h2 {
  text-align: center;
  font-size: 32px;
  margin-bottom: 40px;
  color: #1da1f2; /* Highlighted blue */
}

/* Timeline */
.timeline {
  display: flex;
  flex-direction: column;
  gap: 30px;
  max-width: 900px;
  margin: 0 auto;
  position: relative;
}

.timeline::before {
  content: "";
  position: absolute;
  left: 20px;
  top: 0;
  height: 100%;
  width: 2px;
  background-color: #1da1f2; /* Blue timeline line */
}

/* Timeline Item */
.timeline-item {
  display: flex;
  align-items: flex-start;
  gap: 20px;
  position: relative;
  padding-left: 60px;
}

.timeline-item::before {
  content: "";
  position: absolute;
  left: 10px;
  top: 8px;
  width: 12px;
  height: 12px;
  background-color: #1da1f2; /* Blue circle */
  border-radius: 50%;
  box-shadow: 0 0 8px rgba(29, 161, 242, 0.5);
}

/* Timeline Date */
.timeline-date {
  color: #ccc; /* Light gray */
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 5px;
}

/* Timeline Content */
.timeline-content {
  background-color: #242f40; /* Slightly darker background for each card */
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  flex: 1;
}

.timeline-content h3 {
  font-size: 20px;
  color: #1da1f2; /* Bright blue for institution names */
  margin-bottom: 8px;
}

.timeline-content p {
  font-size: 14px;
  color: #ddd; /* Light gray for descriptions */
  margin: 0;
}

/* Education Logo */
.education-logo {
  width: 90px;
  height: 50px;
  margin-bottom: 10px;
  border-radius: 50%;
  background-color: #ffffff; /* White background for logos */
  padding: 5px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* Responsive Design */
@media (max-width: 768px) {
  .timeline-item {
    padding-left: 40px;
  }

  .timeline-date {
    font-size: 12px;
  }

  .timeline-content {
    padding: 15px;
  }

  .education-logo {
    width: 30px;
    height: 30px;
  }
}
/* Skills Section */
.skills-section {
  background-color: #1b2430; /* Dark background */
  color: white;
  padding: 60px 20px;
  text-align: center;
}

.skills-section h2 {
  font-size: 32px;
  margin-bottom: 40px;
