}

/* Skills Container */
.skills-container {
  background-color: #242f40; /* Slightly darker box */
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.5); /* Shadow for the box */
  max-width: 900px;
  margin: 0 auto; /* Center the box */
}

/* Skills Grid */
.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  gap: 20px;
}

/* Skill Item */
.skill-item {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skill-item img {
  width: 50px;
  height: 50px;
  margin-bottom: 10px;
  transition: transform 0.3s;
}

.skill-item img:hover {
  transform: scale(1.1); /* Slight zoom effect */
}

.skill-item p {
  font-size: 14px;
  color: #ddd; /* Light gray text */
  margin: 0;
}
